%button {
    width: 129px;
    height: 44px;
    border-radius: 5px;
    color: $headerTextColor;
    &:hover,
    &:focus {
        transform: scale(1.1);
    }
    @media screen and (min-width: 768px) {
        width: 136px;
    }
}
.btn-is-active {
    @extend %button;
    background-color: $accentColor;
}
.btn-not-active {
    @extend %button;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid $headerTextColor;
}

%button + %button {
    margin-left: 13px;
}

.button-modal-is-active {
    @extend %button;
    font-size: 12px;
    line-height: 16px;
    min-width: 105px;
    background-color: $accentColor;
    color: $headerTextColor;
}

.button-modal {
    @extend %button;
    font-size: 12px;
    line-height: 16px;
    border: 1px solid $filmTitleColor;
    color: $filmTitleColor;
    min-width: 105px;
}

.button-container {
    display: flex;
    @media screen and (max-width: 767px) {
        justify-content: center; 
        
    }
}